$primary-color: orange;

h1 {
    color: $primary-color; 
}
/*root: Bootswatch - cosmo */
:root {
  --blue: #2780e3;
  --indigo: #6610f2;
  --purple: #613d7c;
  --pink: #e83e8c;
  --red: #ff0039;
  --orange: #f0ad4e;
  --yellow: #ff7518;
  --green: #3fb618;
  --teal: #20c997;
  --cyan: #9954bb;
  --white: #fff;
  --gray: #868e96;
  --gray-dark: #373a3c;
  --primary: #2780e3;
  --secondary: #373a3c;
  --success: #3fb618;
  --info: #9954bb;
  --warning: #ff7518;
  --danger: #ff0039;
  --light: #f8f9fa;
  --dark: #373a3c;
}

/* Elements */
  html {
    font-size: 16px;
  }
  body {
    font-family: Verdana, Tahoma, sans-serif, Helvetica, sans-serif;
    overflow: hidden;
    background: black;
}

.btn:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.fitContent {
  width: fit-content !important;
}
.modal-xl {
  max-width: 70%;
}

/* Floater classes */
  .isSelected {
    border: 3px solid lime !important;
  }
  .floater {
    position: absolute;
    box-sizing: border-box;
    resize: both;
    overflow: hidden;
    border: 1px solid slategray;
    border-radius: 0.5rem;
}

/* Font & text-size classes */
  .smallCaps {
    font-variant: small-caps;
  }
  .spaceWide {
    letter-spacing: 3px;
  }
  .text120 {
    font-size: 120%;
  }
  .text140 {
    font-size: 140%;
  }
  .text160 {
    font-size: 160%;
  }
  .text180 {
    font-size: 180%;
  }
  .text200 {
    font-size: 200%;
  }
  .text220 {
    font-size: 220%;
  }
  .text240 {
    font-size: 240%;
}

/* Tooltip classes */
  .tooltip.show {
    opacity: 1;
  }
  .tooltip-inner {
    max-width: unset;
    padding: 0;
    text-align: left;
    background-color: white;
    color: black;
    border: 1px solid #cd0200;
  }
  .tooltip.bs-tooltip-right .arrow:before {
    border-right-color: #cd0200 !important;
  }
  .tooltip.bs-tooltip-left .arrow:before {
    border-left-color: #cd0200 !important;
  }
  .tooltip.bs-tooltip-bottom .arrow:before {
    border-bottom-color: #cd0200 !important;
  }
  .tooltip.bs-tooltip-top .arrow:before {
    border-top-color: #cd0200 !important;
}

/* Misc id's */
  #collageWrapper {
    opacity: 0.01;
    height: 100vh;
  }
  #loadingGif {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    z-index: 1000;
  }
  #displayOrderBtn {
    display: none;
    position: absolute;
    top: 1rem;
    left: 50%;
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 1000;
  }
  #navdropdown:hover {
    text-decoration: none;
  }
  #offscreenImgLoader {
    position: absolute;
    top: 180vh;
}

/* Help Modal */
  #helpModal {
    font-size: 280% !important;
    font-weight: 900 !important;
    letter-spacing: 1px;
  }
  #helpModal * {
    color: black;
  }
  #helpModal .nav-tabs {
    border-bottom: 2px solid var(--danger);
  }
  #helpModal .nav-tabs .nav-item {
    margin-bottom: -2px !important;
  }
  #helpModal .nav-tabs .nav-link.active {
    border-bottom: none;
    border-color: var(--danger);
    border-width: 2px;
  }
  #helpModal .nav-tabs .nav-link:not(.active):hover {
    color: var(--success) !important;
    border: none !important;
  }
  #helpModal .tab-content>.tab-pane.active {
    border: 2px solid var(--danger);
    border-top: none;
  }
  #quickstart div > p:last-child {
    background-color: yellow;
  }
  
  #howtoedit > div:not(:first-child) div p:last-child,
  #howtoedit > div.w-50.mt-4.mx-auto.text-center > p:last-child {
    background-color: yellow;
  }
  // #howtoedit > div:not(:first-child) div p:last-child {
  //   padding: 0.75rem;
  // }

/* Loading Dock */
  .loadingDock_init {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 66.6vw;
    height: 66.6vh;
    margin: 0;
    top: 18vh;
    left: 16.65vw;
    justify-content: center;
    background-color: black !important;
    border-width: 0.25rem !important;
    border-style: ridge;
    z-index: 1;
  }
  .loadingDock_transition {
    position: absolute;
    display: flex;
    width: 100vw;
    height: 100vh;
    margin: 0;
    top: 0;
    left: 0;
    transition: top 2s, left 2s, width 2s, height 2s;
    justify-content: center;
    background-color: black !important;
    border-width: 0.25rem !important;
    border-style: ridge;
  }
  #selectImagesBtn {
    /* File uploader - hide ugly <input> element */
    width: 0;
    height: 0;
    opacity: 0;
  }
  #createCollageBtn {
    display: none;
}
